import React from 'react';
import './App.css';
import {HashRouter as Router, Switch, Route, Redirect,} from "react-router-dom";
import {HeaderWidget} from "./widget/header";
import styled from "styled-components";
import {MainPage} from "./view/mint/mint";
import {RedeemPage} from "./view/redeem/redeem";
import {useWallet, UseWalletProvider} from 'use-wallet'
import {AppConfig} from "./config";
import {Web3Provider} from "./provider/web3_provider";
import {ModelProvider} from "./provider/model_provider";
import {BackdropProvider} from "./provider/backdrop_provider";
import {useModel} from "./hook/use_model";
import {WalletConnect} from "./widget/modal/wallet_connect";
import {ButtonWidget} from "./widget/base/button";
import {Dialog} from "@material-ui/core";
import {Alert} from "@material-ui/lab";
import {StakePage} from "./view/stake/stake";
import {VoteIndexPage} from "./view/vote/vote_inder";
import {ValuePage} from "./view/value/value";
function App() {
  return (
      <Provider>
          <Page/>
      </Provider>

  );
}

const Provider: React.FC = ({children}) => {
    return (
        <UseWalletProvider chainId={AppConfig.chainId} connectors={{
            walletconnect: {rpcUrl: AppConfig.rpcUrl[AppConfig.chainId]}
        }}>
                    <Web3Provider>
                        <ModelProvider>
                            <BackdropProvider>
                                {children}
                            </BackdropProvider>
                        </ModelProvider>
                    </Web3Provider>

        </UseWalletProvider>

    );
}


const Page: React.FC = () => {

    const {status, chainId, networkName} = useWallet();

    const [onConnect,] = useModel(<WalletConnect/>);
    const [open, setOpen] = React.useState(false);
    const handleClose = (value: string) => {
        setOpen(false);
    };
    return (<PubBg>
        {
            status === 'connected' ? <Router>
                <HeaderWidget/>
                <PageStyle>
                    <Switch>
                        {/*<Route path="/bank" component={CouponsIndexPaeg}/>*/}
                        {/*<Route path="/launch_pahe" component={LaunchPahe}/>*/}
                        <Route path="/main" component={MainPage}/>
                        <Route path="/redeem" component={RedeemPage}/>
                        <Route path="/stake" component={StakePage}/>
                        <Route path="/vote" component={VoteIndexPage}/>
                        <Route path="/value" component={ValuePage}/>
                        {/*<Route path="/bonds" component={BondsRouter}/>*/}
                        {/*<Route path="/boardroom" component={BoardroomRouterPage}/>*/}
                        {/*<Route path="/test" component={TestPage}/>*/}
                        <Redirect to="/main"/>
                    </Switch>

                </PageStyle>
            </Router> : <div
                style={{
                    alignItems: 'center',
                    display: 'flex',
                    flex: 1,
                    justifyContent: 'center',
                    height: '100vh'
                }}
            >
                <div style={{width:"200px"}} >
                    <ButtonWidget
                        onClick={() => {

                            // @ts-ignore
                            if (window['ethereum'].networkVersion != chainId) {
                                setOpen(true);
                                return;
                            } else {
                                onConnect();
                            }
                        }}
                        text="🔓 Unlock Wallet"
                    />
                </div>

                <Dialog onClose={handleClose} open={open}>
                    <Alert severity="error"> Please switch to {networkName} network!</Alert>
                </Dialog>
            </div>

        }
    </PubBg>);
};

const PubBg = styled.div`
`
const PageStyle = styled.div`
  position: initial;
  min-height: 85vh;
`

export default App;
