import {WapWidget} from "../../../widget/base/wap";
import React from "react";
import styled from "styled-components";
import {TopCellWidget} from "../widget/top_cell_widget";
import {useHistory} from "react-router-dom";

export const VoteHomePage: React.FC = () => {

    let history = useHistory();
    const handleClick =  (id:string)=> {
        history.push('/vote/details/'+id);
    }
    return (
        <WapWidget title={'Proposals'}>
            <TitleStyle>
                <p>By staking dTokens you can vote on any active Native token’s proposals.</p>
            </TitleStyle>
            <div>
                <HeightWidget />

                <div onClick={()=>handleClick('1')}>
                   <TopCellWidget model={{isPass:false}}/>
               </div>
                <HeightWidget />
               <div onClick={()=>handleClick('3')}>
                   <TopCellWidget model={{isPass:true}}/>
               </div>
                <HeightWidget />

                <div onClick={()=>handleClick('4')}>
                   <TopCellWidget model={{isPass:false}}/>
               </div>
            </div>
        </WapWidget>



    );
}

const HeightWidget = styled.div`
height: 20px;
`

const TitleStyle = styled.div`
  border-bottom: 1px solid #E7E9ED;
  p{
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 400;
    color: #21247F;
    opacity: 0.8;
  }
  
`
